import React from "react";
import DPIController from "./DPIController";
import ItemInput from "./ItemInput";
import ItemDobleInput from "./ItemDobleInput";
import ModalInfo from "./ModalInfo";

const OptionLabel = ({
  printer,
  setDpi,
  dpi,
  dots,
  setDots,
  mm,
  radiusLabel,
  setRadiusLabel,
  widthLabel,
  setWidthLabel,
  heightLabel,
  setHeightLabel,
  factorPrinter,
  checkFactorPrinter,
  referenceX,
  referenceY,
  setReferenceX,
  setReferenceY,
  marginLeftRightLabel,
  setMarginLeftRightLabel,
}) => {
  return (
    <div className="container-category-input">
      <h2 className="category-input-title">   </h2>
      <DPIController
        setDpi={setDpi}
        dpi={dpi}
        dots={dots}
        setDots={setDots}
        mm={mm}
      />
      <ItemInput
        text="    %"
        value={radiusLabel}
        onChange={(e) => setRadiusLabel(e.target.value)}
      />
      <ItemInput
        text="    "
        value={widthLabel}
        onChange={(e) => setWidthLabel(e.target.value)}
      />
      <ItemInput
        text="   "
        value={heightLabel}
        onChange={(e) => setHeightLabel(e.target.value)}
      />
      <ItemInput
        text="     "
        value={marginLeftRightLabel}
        onChange={(e) => setMarginLeftRightLabel(e.target.value)}
      />
      <div className="factor-box">
        <ItemDobleInput
          title="REFERENCE"
          value_one={referenceX}
          onChange_one={(e) => setReferenceX(e.target.value)}
          text_one=" X"
          value_two={referenceY}
          onChange_two={(e) => setReferenceY(e.target.value)}
          text_two=" Y"
        />
        <ModalInfo>
          {" "}
          REFERENCE -     'X'  'Y' .   
          dpi.
        </ModalInfo>
      </div>
      <div className="factor-box">
        {" "}
        <ItemInput
          text=" "
          value={factorPrinter}
          onChange={checkFactorPrinter}
        />
        <ModalInfo>
          {" "}
            -   ,  
             .    
               DataMatrix .  {" "}
          {printer.model}        - 
          {printer.factor}    {widthLabel} * {heightLabel} .
        </ModalInfo>
      </div>
    </div>
  );
};

export default OptionLabel;
